.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header {
  margin-bottom: 20px; /* Add margin-bottom here */
}
.nav-list {
  display: flex;
  list-style: none;
  padding: 0;
  justify-content: center;
  margin: 0 -10px;
}

.nav-item {
  margin: 0 10px;
}

.task-section {
  width: 80%;
  max-width: 800px; /* Optional: You can adjust the max-width to fit your needs */
  margin: 0 auto; /* Center the task section horizontally */
  padding: 20px;
  border: 1px solid black;
}

.task-item {
  border: 1px solid black;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.task-list {
  list-style: none;
  padding: 0;
}

.task-item .delete-button {
  background-color: red;
  color: white;
}

.task-item .complete-button {
  background-color: green;
  color: white;
}

.completed-tasks {
  margin-top: 10px;
  font-weight: bold;
  color: green;
}

.completed-tasks-container {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

