.notes-section {
    position: absolute;
    right: 150px;
    top: 148px;
    width: 300px;
    height: 80vh;
    background-color: rgba(255, 252, 78, 0.521);
    padding: 20px;
    border-radius: 5px;
    border: 1px solid black; /* Add black border */
    overflow: auto;
    margin-bottom: 20px;
    margin-top: -20px; /* Add a negative margin-top to move the notes section */
  }
  
  .notes-title {
    margin-top: 0;
  }
  
  .notes-textarea {
    width: 100%;
    height: 100%;
    resize: none;
    padding: 10px;
    font-size: 16px;
    line-height: 1.5;
    border: none;
    outline: none;
    color: black;
    background-color: rgba(255, 252, 78, 0); /* Increase opacity here */
    overflow: auto;
  }
  